
  import { defineComponent, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'overview',
    setup() {
      const open = ref(false);
      const { prefixCls } = useDesign('fighting');

      function handleClose() {
        open.value = false;
      }
      function handleOpen() {
        open.value = true;
      }
      return { prefixCls, open, handleClose, handleOpen };
    },
  });
